@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
.index-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
}

.index-container > .search {
    width: 100%;
    padding: 1em;
    text-align: center;
}
  
.index-container > .search > input[type=text] {
    width: 100%;
    padding: 0.5em;
    border: 0;
    border-radius: 0.3em;
}

.index-container .list {
    padding: 1em;
}

.index-container .list > ul {
    list-style: none;
}

.index-container > .list ul li {
    padding: 1em;
    background-color: #FFF;
    margin: 1em 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: nowrap;
}

.index-container > .list ul li > .nome {
    width: 100%;
    max-width: 150px;
    font-size: 1.5em;
    font-weight: bold;
}


.index-container > .list ul li > .alternativas > div {
    width: 100%;
    max-width: 400px;
    padding: 0.5em 0;
}

.index-container > .list ul li > .alternativas > div > label {
    background-color: #333333;
    color: #FFF;
    padding: 0.3em;
    margin: 0.3em 0.3em 0 0;
    float: left;
}

.index-container .scroll-top {
    position: fixed;
    bottom: 1em;
    right: 1em;
    border: 0;
    padding: 0.5em;
    background-color: #333333;
    color: #FFF;
    border-radius: 0.3em;
}

.index-container h3 {
    text-align: center;
}

.index-container .loading, .index-container .vazio {
    margin-top: 0.5em;
}
  
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background-color: #f0f0f5;
    -webkit-font-smoothing: antialised;
}

input, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea {
    width: 100%;
    min-height: 140px;
    height: 60px;
    color: #333;
    border: 1px solid #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}

.button {
    width: 100%;
    height: 60px;
    background-color: #e02041;
    border: 0;
    border-radius: 8px;
    color: #FFF;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link {
    display: flex;
    align-items: center;
    margin-top: 48px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transform: opacity 0.2s;
}

.back-link svg {
    margin-right: 8px;
}

.back-link:hover {
    opacity: 0.8;
}

.root > h2 {
    text-align: center;
    margin-bottom: 0.6em;
    background-color: #333;
    color: #dcdce6;
    text-transform: uppercase;
    padding: 0.4em;
}
